.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.points-block{
  font-size: 30px;
  display: flex;
  justify-content: center;
}
.btn-class{
  width: 150px;
  height: 50px;
}
.image-class{
  width: 100px;
  margin: 50px;
}
.points{
  display: flex;
  flex-direction: column;
}
body{
  background-color: #FDFFE7;
}
.text{
  margin-top: 20px;
  font-size: 32px;
}
